@keyframes wallpaperIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.5;
  }
}
@keyframes desktopIn {
  0% {
    opacity: 0;
    transform: scale(0.96);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
.window {
  width: 40rem;
  height: 30rem;
  border-radius: 7px;
  overflow: hidden;
  box-shadow: 0px 7px 14px rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(0, 0, 0, 0.1);
  background: rgba(0, 0, 0, 0.3);
  position: absolute;
  backdrop-filter: blur(30px);
}
.window .frame {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.window .frame .titlebar {
  height: 2rem;
  width: 100%;
  background: white;
  display: flex;
  flex-direction: row;
  color: black;
}
.window .frame .titlebar .icon {
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.window .frame .titlebar .icon img {
  width: 2rem;
  height: 2rem;
}
.window .frame .titlebar .title {
  flex: 1;
  display: flex;
  align-items: center;
}
.window .frame .titlebar .controls {
  width: 4rem;
  height: 100%;
}
.window .frame .mainWindow {
  flex: 1;
}

body, html {
  background: #2d2a2e;
  font-family: Poppins, Loma, Arial, sans-serif;
  color: white;
  margin: 0;
  padding: 0;
}

.desktop {
  height: calc(100vh - 4rem);
  width: calc(100vw - 4rem);
  margin: 2rem;
  border-radius: 20px;
  box-sizing: border-box;
  box-shadow: 0px 7px 20px rgba(0, 0, 0, 0.5);
  position: relative;
  overflow: hidden;
  animation: desktopIn 0.3s ease forwards;
  background: black;
}
.desktop div.section {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
}
.desktop .wallpaper {
  background-image: url("/src/System/Media/Backgrounds/Displacement.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  animation: wallpaperIn 1s ease forwards;
}
.desktop .icons {
  box-sizing: border-box;
  padding: 2rem;
}
.desktop .windows {
  position: relative;
}